.container {
  border-radius: 16px 0px 16px 16px;
  background: var(
    --Radial,
    radial-gradient(
      142.94% 142.94% at 0% -16.28%,
      #11cf74 4.69%,
      #08c7b0 98.31%
    )
  );
  width: 85%;
  padding: 15px 15px 5px;
  right: 0;
}

.text {
  color: #fff;
  font-size: 13px;
  font-style: normal;
  font-weight: 300;
  letter-spacing: 0.39px;
}

.details {
  display: flex;
  justify-content: end;
  align-items: end;
  gap: 5px;
  color: #fff;
  text-align: right;
  font-size: 10px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: 0.3px;
}
