.object-details {
  display: flex;
  gap: 4px;
  align-items: center;
  color: #ffffff;
}
.object-details__text {
  color: #ffffff;
  font-size: 14px;
  font-weight: 300;
}
.object-details__text--grey {
  color: rgba(37, 43, 92, 0.7);
  font-size: 14px;
  font-weight: 500;
}
.object-details__icon {
  fill: #ffffff;
  width: 18px;
}
.object-details__icon--grey {
  fill: rgba(37, 43, 92, 0.7);
  width: 15px;
}/*# sourceMappingURL=objectDetails.css.map */