@import url('../../fonts.css');

* {
  margin: 0;
  padding: 0;
  font-family: 'New Rubrik';
}

.swipe {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: transform 0.1s linear;
}

.card {
  &__botom {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    bottom: 0;
    height: 20%;
    width: 100%;
    border-radius: 0 0 20px 20px;
    background: linear-gradient(to top,
        rgba(0, 0, 0, 0.8) 0%,
        rgba(0, 0, 0, 0.5) 60%,
        rgba(0, 0, 0, 0.3) 80%,
        rgba(0, 0, 0, 0) 100%);
  }

  &__top{
    display: flex;
    justify-content: space-between;
    gap: 4px;
    // padding-top: 9px;
    padding-left: 19px;
    padding-right: 19px;
  }

  &__price-status{
    display: flex;
    gap: 15px;
    align-items: center;
  }

  &__price{
    color: #ffffff;
    font-size: 25px;
    font-weight: 300;
  }

  &__status{
    color: #ffffff;
    font-size: 14px;
    font-weight: 300;
  }

  &__buttons{
    display: flex;
    gap: 17px;
  }

  &__button{
    position: relative;
    border: none;
    background: none;

    &-img{
      transition: all 0.4s;
    }

    :active{
      transform: scale(1.4);
    }
  }

  &__like-count{
    position: absolute;
    font-size: 12px;
    color: #ffffff;
    top: 22%;
    left: 22%;
    pointer-events: none;
  }

  &__info{
    display: flex;
    justify-content: space-between;
    padding-left: 19px;
    padding-right: 19px;
    // padding-top: 5px;

    &-squere{
      max-width: 17px;
    }
  }

  &__info-time{
    font-size: 13px;
    font-weight: 300;
    color: #ffffff;
  }
}

.button__back {
  z-index: 4;
  position: absolute;
  bottom: 84px;
  left: 20px;
  border: none;
  cursor: pointer;
  color: #fff;
  background-color: rgb(17, 207, 36);
  padding: 5px 10px;
  border-radius: 50%;

  &:disabled {
    cursor: not-allowed;
    opacity: 0.5;
  }
}

.swipe-feedback {
  font-size: 24px;
  font-weight: bold;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 9%;
  // height: 94.5%;
  border-radius: 50px;
  color: white;
  padding: 5px 19px;
  pointer-events: none; 
  display: none;
  transition: all 0.3s;
  background-color: rgba(255, 255, 255, 0.9);
  
  &.left{
    border: 2px solid #c40000;
    padding:8px 19px 1px 19px;
  }

  &.right{
    border: 2px solid #00c44b;
  }

  &__img{
    width: 100%;
  }

}
