.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 45px 0 45px;
  height: 7vh;
  position: relative;

  &::before {
    content: "";
    width: 9px;
    height: 9px;
    background-color: rgb(0, 153, 25);
    position: absolute;
    top: 10px;
    right: 49px;
    border-radius: 50%;
  }
}

.heading {
  font-size: 15px;
  font-weight: 500;
  letter-spacing: 0.45px;
}

.back {
  background: none;
  border: none;
}

.avatar {
  width: 45px;
  height: 45px;
  object-fit: cover;
  border-radius: 50px;
}

