.loading_spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.logo {
  position: absolute;
  width: 100px;
}

.spinner {
  border: 4px solid rgba(102, 102, 102, 0.1);
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border-left-color: rgb(0, 167, 111);
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}/*# sourceMappingURL=loadingSpiner.module.css.map */